.reserv {
  // width: 180px;
  padding: 0;
  margin: 0 auto;
  margin-top: 10px;
  font-size: 14px;
  line-height: 26px;
  font-weight: 400;
  text-align: center;
}

.reserv__text {
  position: relative;
  display: block;
  width: 310px;
  padding: 0;
  margin: 0 auto;
  color: #aaaaaa;
  text-transform: none;
  text-decoration: none; /* Убирает подчеркивание */
  border-top: 1px solid #aaaaaa;
  cursor: default; /* Стандартный курсор */

  &:hover::after {
    width: 100%;/* Для ::after при наведении устанавливаем подчеркивание */
  }
}

.reserv__text::after {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 0;
  content: ".";
  color: transparent;
  background: #aaaaaa;
  height: 1px;
  transition: all .5s;
  -webkit-transition: all .5s;
}


@media (min-width: $tablet-width) {

  .reserv {
    // width: 50%;
    padding: 5px 0;
    margin: 0 auto 10px;
    // margin-right: 150px;
    font-size: 14px;
    line-height: 26px;
    font-weight: 400;
    text-align: center;
  
    // border-top: 1px solid #aaaaaa;
  }

  .reserv__text {
    width: 350px;
    text-transform: uppercase;    
    border-top: none;
  }
}


@media (min-width: $desktop-width) {

  .reserv {
    font-size: 16px;
    line-height: 26px;
    font-weight: 400;
  
    // border-top: 1px solid #aaaaaa;
  }

  .reserv__text {
    width: 500px;

    text-transform: uppercase;
  }

}
